<template>








      <v-container>

       
        <v-layout row wrap>		
          <v-flex xs12><img src="../assets/logo-chsli.png" style="max-width:250px;" /></v-flex>        
        </v-layout>        

        <v-layout row wrap>		
          <v-flex xs12><strong><span style="font-size:1.5em;color:#336699;">Office Manager Update - Provider List:</span></strong></v-flex>
        </v-layout>


        <v-layout row wrap>	 
            <v-flex xs12 md1></v-flex>
            <v-flex xs12 md3>Provider Name:</v-flex>
            <v-flex xs12 md2>NPI:</v-flex>
            <v-flex xs12 md3>Specialty:</v-flex>
            <v-flex xs12 md3>Department Name:</v-flex>             
        </v-layout> 


        <v-layout row wrap v-for="provider in providers" :key="provider.name">	
            <v-flex xs12 md1><a :href="'/managerupdate?npi=' + provider.npi">Edit</a></v-flex>
            
            <v-flex xs12 md3>{{ provider.name }}</v-flex>
            <v-flex xs12 md2>{{ provider.npi }}</v-flex>
            <v-flex xs12 md3>{{ provider.specialty }}</v-flex>
            <v-flex xs12 md3>{{ provider.department_name }}</v-flex>            
        </v-layout> 

        <!--div>
          <ValidationProvider v-slot="v">
            <input v-model="ref.value" type="text">
          </ValidationProvider>
        </div-->

        <v-snackbar v-model="snackbar.show" :multi-line="true" :bottom="true" :color="snackbar.color">
          {{ snackbar.message }}
          <v-btn color="#bbb" text @click="snackbar.show = false">Close</v-btn>
        </v-snackbar>   

      </v-container>

 
</template>

<script>
import { ValidationProvider } from 'vee-validate';
import axios from "axios";

export default {
  name: "ManagerUpdate", 
  data: () => ({

    providers: [],

    ref: {}, 
    menuEffectiveDate: false,
    NPI : '',
/*    loading : '',
      status: '',
*/      
    snackbar: {
      show: false,
      color: "green",
      message: "",
      bottom: true,
      timeout: 3000
    }       
	}),
  components: {
    //ValidationProvider
  },

  beforeMount: function() {
  
    const params = window.location.search.substring(1).split("&");
    for (var i = 0; i < params.length; i++) {
      var pair = params[i].split("=");
      if (decodeURIComponent(pair[0]) === "ad") {
        this.ad = decodeURIComponent(pair[1]);
      }
    }
    
    //this.$refs.form.reset();
    this.ref = this.initRef();
    this.getPhyInfo();
  },  

  mounted() {
  },  

  methods: {
    getPhyInfo() {
      //const urlget = "http://localhost:3100/managerupdate/"+this.ad;           //dev
      //const urlget = "https://www030.chsli.org:3016/managerupdate/"+this.npi;  //tst
      const urlget = "https://www030.chsli.org:3010/managerupdate/"+this.ad;   //uat
      //GET
      axios.get(urlget)
      .then(res =>{
        /*
        console.log("res="+JSON.stringify(res.data[0].providerData[0]));
        console.log("data length="+res.data[0].providerData.length);
        */
        this.providers = [];
        var i;
        for (i = 0; i < res.data[0].providerData.length; i++) {
          if (JSON.stringify(res.data[0].providerData[i].Location[0].OfficeManagerAD).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].OfficeManagerAD).length-1))==(this.ad)) {
            this.providers.splice(0,0,{ name: JSON.stringify(res.data[0].providerData[i].Prov_name).substring(1, (JSON.stringify(res.data[0].providerData[i].Prov_name).length-1)), npi: JSON.stringify(res.data[0].providerData[i].NPI).substring(1, (JSON.stringify(res.data[0].providerData[i].NPI).length-1)), specialty: JSON.stringify(res.data[0].providerData[i].specialty).substring(1, (JSON.stringify(res.data[0].providerData[i].specialty).length-1)), department_name: JSON.stringify(res.data[0].providerData[i].Location[0].Department_name).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].Department_name).length-1)) });
          }  
        }
        //console.log("this.ad="+this.ad);

      }).catch(err => {
        console.log(err);  
      })
    }, 
    

    initializeRef() { 
    }, 
   
    initRef() {
      return {
        providerName: "", 
        providerNPI: "", 
        providerAddress: "",
        providerCity: "",
        providerState: "",
        providerZip: "",
        providerPhone: "",
        providerFax: "",
      }
    }
  }

};





</script> 