//router/index.js
import Vue from 'vue'
import Router from 'vue-router'
import ManagerUpdateList from '@/components/ManagerUpdateList'
import ManagerUpdate from '@/components/ManagerUpdate'

Vue.use(Router) 

export default new Router({
  mode: 'history',
  routes: [
    {
        path: '', 
        name: 'Home'
    },       
    {
        path: '/managerupdate', 
        name: 'ManagerUpdate', 
        component: ManagerUpdate 
      },      
    {  
      path: '/managerupdatelist', 
      name: 'ManagerUpdateList', 
      component: ManagerUpdateList 
    }
  ]
})