<template>
    <form @submit.prevent="validateBeforeSubmit">
      <v-container>
        <v-layout row wrap>		
          <v-flex xs12><img src="../assets/logo-chsli.png" style="max-width:250px;" /></v-flex>        
        </v-layout>        

        <v-layout row wrap>		
          <v-flex xs12><strong><span style="font-size:1.5em;color:#336699;">Office Manager Update</span></strong></v-flex>
        </v-layout>

        <v-layout row wrap>		
          <v-flex xs12 md8><v-text-field label="Provider Name" v-model="ref.providerName" name="Provider Name" ></v-text-field></v-flex>
          <v-flex xs12 md4><v-text-field label="NPI" v-model="ref.providerNPI" name="NPI" ></v-text-field></v-flex>
        </v-layout>

        <v-layout row wrap>		
          <v-flex xs12><v-text-field label="Location ID" v-model="ref.providerLocationID" name="LocationID" ></v-text-field></v-flex>
        </v-layout> 

        <v-layout row wrap>		
          <v-flex xs12><v-text-field label="Address" v-model="ref.providerAddress" name="Address" ></v-text-field></v-flex>
        </v-layout>      

        <v-layout row wrap>		
          <v-flex xs12><v-text-field label="City" v-model="ref.providerCity" name="City" ></v-text-field></v-flex>
        </v-layout>         

        <v-layout row wrap>		
          <v-flex xs12 md6><v-text-field label="State" v-model="ref.providerState" name="State" ></v-text-field></v-flex>
          <v-flex xs12 md6><v-text-field label="Zip" v-model="ref.providerZip" name="Zip" ></v-text-field></v-flex>
        </v-layout> 

        <v-layout row wrap>		
          <v-flex xs12 md6><v-text-field label="Phone" v-model="ref.providerPhone" name="Phone" ></v-text-field></v-flex>
          <v-flex xs12 md6><v-text-field label="Fax" v-model="ref.providerFax" name="Fax" ></v-text-field></v-flex>
        </v-layout> 

        <!--v-layout row wrap>		
          <v-flex xs12 md6><v-text-field label="Current Loyalty" v-model="ref.providerCurrentLoyalty" name="Current Loyalty" ></v-text-field></v-flex>
          <v-flex xs12 md6><v-text-field label="Effective Date" v-model="ref.providerEffectiveDate" name="Effective Date" ></v-text-field></v-flex>
        </v-layout>            

        <v-layout row wrap>
          <v-flex xs12>        
            <v-radio-group v-model="ref.providerNewLoyalty" row>
              <span style="margin-right:8px;color:rgba(0,0,0,0.54);">New Loyalty: </span>
              <v-radio label="Loyal" value="Loyal"></v-radio>
              <v-radio label="Splitter" value="Splitter"></v-radio>
              <v-radio label="Opportunity" value="Opportunity"></v-radio>
              <v-radio label="Out Of Network" value="Out Of Network"></v-radio>              
            </v-radio-group>
          </v-flex>  
        </v-layout-->            

        <!--v-layout row wrap>
          <v-flex xs12>
            <v-menu
              ref="menuEffectiveDate"
              v-model="menuEffectiveDate"              
              :close-on-content-click="true"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field
                  id="effectiveDate"
                  v-model="ref.menuEffectiveDate"
                  slot="activator"                  
                  label="Effective Date"
                  name="Effective Date"
                  prepend-icon="event"   
                  readonly
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                ref="picker"   
                v-model="ref.menuEffectiveDate"
                max="2250-12-31"
                min="1980-01-01">
              </v-date-picker>
            </v-menu>
          </v-flex>
        </v-layout-->




        <!--v-layout row wrap>		          
          <v-flex xs12>
            <ValidationProvider v-slot="v">
              <v-text-field v-model="ref.providerReasonForChange" label="Reason for Change/Comment" name="Reason for Change/Comment" ></v-text-field>
              <span>{{ v.errors[0] }}</span>
            </ValidationProvider>
          </v-flex>          
        </v-layout-->

        <!--div>
          <ValidationProvider v-slot="v">
            <input v-model="ref.value" type="text">
          </ValidationProvider>
        </div-->

        <v-layout row wrap>
          <v-flex xs12>
              <v-btn @click="submitForm" color="normal">Submit Update</v-btn>              
          </v-flex>
        </v-layout>

        <v-snackbar v-model="snackbar.show" :multi-line="true" :bottom="true" :color="snackbar.color">
          {{ snackbar.message }}
          <v-btn color="#bbb" text @click="snackbar.show = false">Close</v-btn>
        </v-snackbar>   

      </v-container>
    </form>
 
</template>

<script>
import { ValidationProvider } from 'vee-validate';
import axios from "axios";

export default {
  name: "ManagerUpdate", 
  data: () => ({

    providers: [],

    ref: {}, 
//    menuEffectiveDate: false,
    NPI : '',
/*    loading : '',
      status: '',
*/      
    snackbar: {
      show: false,
      color: "green",
      message: "",
      bottom: true,
      timeout: 3000
    }       
	}),
  components: {
    //ValidationProvider
  },

  beforeMount: function() {
  
    const params = window.location.search.substring(1).split("&");
    for (var i = 0; i < params.length; i++) {
      var pair = params[i].split("=");
      if (decodeURIComponent(pair[0]) === "npi") {
        this.npi = decodeURIComponent(pair[1]);
      }
    }
    
    //this.$refs.form.reset();
    this.ref = this.initRef();
    this.getPhyInfo();
  },  

  mounted() {
  },  

  methods: {
    getPhyInfo() {
      //const urlget = "http://localhost:3100/managerupdate/"+this.npi;          //dev
      //const urlget = "https://www030.chsli.org:3016/managerupdate/"+this.npi;  //tst
      const urlget = "https://www030.chsli.org:3010/managerupdate/"+this.npi;   //uat

      //GET
      axios.get(urlget)
      .then(res =>{
        this.providers = [];
        var i;
        for (i = 0; i < res.data[0].providerData.length; i++) {
          if (JSON.stringify(res.data[0].providerData[i].NPI).substring(1, (JSON.stringify(res.data[0].providerData[i].NPI).length-1))==(this.npi)) {
//            this.providers.splice(0,0,{ name: JSON.stringify(res.data[0].providerData[i].Prov_name).substring(1, (JSON.stringify(res.data[0].providerData[i].Prov_name).length-1)), npi: JSON.stringify(res.data[0].providerData[i].NPI).substring(1, (JSON.stringify(res.data[0].providerData[i].NPI).length-1)), specialty: JSON.stringify(res.data[0].providerData[i].specialty).substring(1, (JSON.stringify(res.data[0].providerData[i].specialty).length-1)), department_name: JSON.stringify(res.data[0].providerData[i].Location[0].Department_name).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].Department_name).length-1)) });
            this.ref.providerName = JSON.stringify(res.data[0].providerData[i].Prov_name).substring(1, (JSON.stringify(res.data[0].providerData[i].Prov_name).length-1));
            this.ref.providerNPI = JSON.stringify(res.data[0].providerData[i].NPI).substring(1, (JSON.stringify(res.data[0].providerData[i].NPI).length-1));
            this.ref.providerLocationID = JSON.stringify(res.data[0].providerData[i].Location[0].LocationID).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].LocationID).length-1));
            this.ref.providerAddress = JSON.stringify(res.data[0].providerData[i].Location[0].Address).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].Address).length-1));
            this.ref.providerCity = JSON.stringify(res.data[0].providerData[i].Location[0].City).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].City).length-1));
            this.ref.providerState = JSON.stringify(res.data[0].providerData[i].Location[0].state).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].state).length-1));
            this.ref.providerZip = JSON.stringify(res.data[0].providerData[i].Location[0].ZIP).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].ZIP).length-1));
            this.ref.providerPhone = JSON.stringify(res.data[0].providerData[i].Location[0].Phone).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].Phone).length-1));
            this.ref.providerFax = JSON.stringify(res.data[0].providerData[i].Location[0].Fax).substring(1, (JSON.stringify(res.data[0].providerData[i].Location[0].Fax).length-1));
          }  
        }

      }).catch(err => {
        console.log(err);  
      })
    }, 
    submitForm: function () {
      const urlpost = "http://localhost:3100/managerupdate/"+this.npi;        //dev   
      //const urlpost = "https://www030.chsli.org:3010/managerupdate/"+this.npi;//uat
      //const urlpost = "https://www030.chsli.org:3016/managerupdate/"+this.npi;//tst             

      this.loading = "Updating physician information, please wait...."
      //let formData = new FormData();
      /*for (let key in this.ref) {
        //console.log("key="+key+"; this.ref[key]="+this.ref[key]);
        //formData.append('\''+key+'\'', '\''+this.ref[key]+'\'');
        formData.append(key, this.ref[key]);
      }*/
      //console.log("this.ref="+JSON.stringify(this.ref));
      //console.log("formData="+JSON.stringify(formData));

        axios
          //.post(urlpost, formData)
          .post(urlpost, this.ref)          
          .then(response => {      
            //console.log("response="+JSON.stringify(response));  
            //console.log("response.status="+ JSON.stringify(response.status));          
            if (JSON.stringify(response.data).includes("Error:")) {
              this.snackbar.color = "red";
              this.snackbar.message = JSON.stringify(response.data);
              this.snackbar.show = true;
              return false;
            } 
            this.snackbar.color = "green";             
            this.snackbar.message = 
              "The information has been updated. Thank you.";               
            this.snackbar.show = true; 
            /*this.ref = this.initializeRef();
            this.$refs.form.reset();*/   
            //this.initRef();      
            //this.getPhyInfo();                    
            return response;//added to advoid response defined but not used error              
        })
          .catch(error => {
            this.snackbar.color = "red";
            this.snackbar.message =
              "We're sorry, the loyalty information cannot be updated at the moment, please try again later. " +
              error;
            this.snackbar.show = true;
        });
    },

    initializeRef() { 
    }, 

    initRef() {
      return {
        providerName: "", 
        providerNPI: "",
        providerLocationID: "", 
        providerAddress: "",
        providerCity: "",
        providerState: "",
        providerZip: "",
        providerPhone: "",
        providerFax: "",
      }
    }
  }

};

</script> 